    </div>

    <div class="col-md-6">
      <q-card  square flat bordered class="my-card q-ma-sm"> 
        <q-separator />

        <q-card-actions vertical>
          <div class="q-pa-md">
            <q-table
              title="Table"
              :data="data"
              :columns="columns"
              row-key="id"
              :pagination.sync="pagination"
              :loading="loading"
              :filter="filter"
              @request="onRequest"
              binary-state-sort
              table-style="height:380px"
            >
              <template v-slot:top-right>
                <q-input
                  borderless
                  dense
                  debounce="300"
                  v-model="filter"
                  placeholder="Search"
                >
                  <template v-slot:append>
                    <q-icon name="search" />
                  </template>
                </q-input>
              </template>
            </q-table>
          </div>
        </q-card-actions>
      </q-card>
    </div>

   <div class="col-md-6">
      <q-card flat bordered class="my-card q-ma-sm" square>

  <q-card-section dark inset>

 <div class="text-h6">Profile Admin</div>
          <div class="text-subtitle2">
           
          </div>

 <table class="positionadmin">
        <tr>
          <td>ID:</td>
          <td>S4675</td>
        </tr>
        <tr>
          <td>Name:</td>
          <td>Aiman Hakim</td>
