     </div>


    
  
    
    
  </div>
</template>



<script>
import isEmail from "validator/lib/isEmail";

export default {
  name: "usermanagement",
  data() {
    return {
      filter: "",
      loading: false,
      username: "",
      name: "",
      email: "",
      password: "",
      confirmationpass: "",
      update: false,

      pagination: {
        sortBy: "name",
        descending: false,
        page: 1,
        rowsPerPage: 3,
        rowsNumber: 10,
      },

      //name every Column
      columns: [
        { name: "User", align: "center", label: "No", field: "userId" },
        {
          name: "id",
          align: "center",
          label: "Username",
          field: "id",
        },
        { name: "title", align: "center", label: "Name", field: "title" },

        { name: "completed", label: "Email", field: "completed" },
      ],
      data: [],

      original: [{}],
    };
  },

  mounted() {
    // get initial data from server (1st page)
    this.onRequest({
      pagination: this.pagination,
      filter: undefined,
    });
  },

  methods: {
    onRequest(props) {
      const { page, rowsPerPage, sortBy, descending } = props.pagination;
      const filter = props.filter;

      this.loading = true;
      this.$store.dispatch("cleaner/display").then((response) => {
        this.original = response.data;
        console.log(response.data);
        setTimeout(() => {
          // update rowsCount with appropriate value
          this.pagination.rowsNumber = this.getRowsNumberCount(filter);

          // get all rows if "All" (0) is selected
          const fetchCount =
            rowsPerPage === 0 ? this.pagination.rowsNumber : rowsPerPage;

          // calculate starting row of data
          const startRow = (page - 1) * rowsPerPage;

          // fetch data from "server"
          const returnedData = this.fetchFromServer(
            startRow,
            fetchCount,
            filter,
            sortBy,
            descending
          );

          // clear out existing data and add new
          this.data.splice(0, this.data.length, ...returnedData);

          // don't forget to update local pagination object
          this.pagination.page = page;
          this.pagination.rowsPerPage = rowsPerPage;
          this.pagination.sortBy = sortBy;
          this.pagination.descending = descending;

          // ...and turn of loading indicator
          this.loading = false;
        }, 1500).catch((error) => {
          this.$q.notify({
            message: error.response.data.error,
            color: "negative",
            icon: "error",
            position: "top",
          });
        });
      });

      // emulate server
    },

    fetchFromServer(startRow, count, filter, sortBy, descending) {
      const data = filter
        ? this.original.filter((row) => row.Name.includes(filter))
        : this.original.slice();

      // handle sortBy
      if (sortBy) {
        const sortFn =
          sortBy === "desc"
            ? descending
              ? (a, b) => (a.name > b.name ? -1 : a.name < b.name ? 1 : 0)
              : (a, b) => (a.name > b.name ? 1 : a.name < b.name ? -1 : 0)
            : descending
            ? (a, b) => parseFloat(b[sortBy]) - parseFloat(a[sortBy])
            : (a, b) => parseFloat(a[sortBy]) - parseFloat(b[sortBy]);
        data.sort(sortFn);
      }

      return data.slice(startRow, startRow + count);
    },

    getRowsNumberCount(filter) {
      if (!filter) {
        return this.original.length;
      }
      let count = 0;
      this.original.forEach((treat) => {
        if (treat.Name.includes(filter)) {
          ++count;
        }
      });
      return count;
    },
    validationEmail(value) {
      return isEmail(value);
    },
    register() {

  const usernameSelector = this.$refs.username;
  const nameSelector = this.$refs.name;
  const emailSelector = this.$refs.email;
   const passwordSelector = this.$refs.password;
 usernameSelector.validate();
 nameSelector.validate();
 emailSelector.validate();
  passwordSelector.validate();

   if (usernameSelector.hasError || nameSelector.hasError||emailSelector.hasError||passwordSelector.hasError) return;
      else{

 let data = {
        username: this.username,
        name: this.name,
        email: this.email,
        password: this.password,
      };

      this.$store
        .dispatch("cleaner/insertdata", data)

        .then(() => {
          this.$q.notify({
            message: "Register Successful",
            color: "blue",
            icon: "check_circle",
            position: "top",
          });
        })

        .catch((error) => {
          this.$q.notify({
            // error.response.data.error,
            message: error.response.data.error,
            color: "negative",
            icon: "error",
            position: "top",
          });
        });
      console.log(data);
      }


     
    },
  },
};
</script>


